import listCell from '@/components/list-cell';
import roleAdviser from './components/role-adviser'
import { mapState } from 'vuex';
import { checkLogin,goLogin } from '@/common/utils.js'
export default {
	components: {
		listCell,
		roleAdviser
	},
	data() {
		return {
			processMaskShow:false,
			role:'adviser',
			showOper: true,
			mesList: [{
					num: 17,
					title: '咨询过',
					path: '/sub_user/pages/consulted/index'
				},
				{
					num: 17,
					title: '报名邀约',
					path: '/sub_user/pages/invited/index'
				},
				{
					num: 17,
					title: '已收藏',
					path: '/sub_user/pages/school-collected/index'
				}
			],
			list: [{
					icon: 'icon-yixiang',
					title: '报读意向',
					tip: '立即设置',
					path: '',
					color: 'rgb(84, 180, 239)'
				},
				{
					icon: 'icon-address',
					title: '通讯地址',
					tip: '立即设置',
					path: '/sub_user/pages/personalInfo/address',
					color: 'rgb(95, 205, 162)'
				},
				{
					icon: 'icon-kefu',
					title: '联系客服',
					path: '/pages/setting/setting',
					color: 'rgb(85, 170, 255)'
				},
				{
					icon: 'icon-wallet',
					title: '商务合作',
					path: '/sub_tim/pages/joinUs/index',
					color: 'rgb(85, 170, 255)'
				},
				{
					icon: 'icon-summary',
					title: '关于我们',
					path: '/sub_user/pages/about/index',
					color: 'rgb(224, 116, 114)'
				},
				{
					icon: 'icon-summary',
					title: '退出登录',
					path: '/pages/login/login',
					color: 'rgb(224, 116, 114)'
				},
			],
			adviserList:[
				{
					icon: 'icon-summary',
					title: '邀约进度',
					path: '/sub_user/pages/invited/index',
					color: 'rgb(224, 116, 114)'
				},
				{
					icon: 'icon-summary',
					title: '运营等级',
					path: '/pages/login/login',
					color: 'rgb(224, 116, 114)'
				},
				{
					icon: 'icon-kefu',
					title: '联系客服',
					path: '/pages/setting/setting',
					color: 'rgb(85, 170, 255)'
				},
				{
					icon: 'icon-summary',
					title: '关于我们',
					path: '/sub_user/pages/about/index',
					color: 'rgb(224, 116, 114)'
				},
			]
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	mounted() {},
	// onHide：监听页面的隐藏
	// 当从当前A页跳转到其他页面，那么A页面处于隐藏状态。
	onHide() {
		this.showOper = true
	},
	methods: {
		// 个人信息跳转
		personalInfoLink() {
			checkLogin().then(() => {
				uni.navigateTo({
					url: '/sub_user/pages/personalInfo/index'
				})
			}).catch(() => {
				// goLogin()
				uni.navigateTo({
					url:'/pages/login/index'
				})
			})
		},
		//菜单跳转
		mesListItemClick(index) {
			uni.navigateTo({
				url: this.mesList[index].path
			})
		},
		//钱包跳转
		walletLink() {
			uni.navigateTo({
				url: '/sub_user/pages/wallet/index'
			})
		},
		//入学进度点击展开
		showOperClick() {
			this.showOper = false
			this.processMaskShow = true
		},
		//遮罩层触摸事件：遮罩层隐藏且入学进度模块收起
		processTouchstart(){
			this.processMaskShow = false
			this.showOper = true
		},
		//兼容PC端上浏览时使用鼠标滚动而不是触摸
		onPageScroll() {
			this.showOper = true
		},
		// 入学进度
		progressDetail() {
			uni.navigateTo({
				url: "/sub_user/pages/progress/index"
			})
		},
		//分享入口
		shareClick() {
			uni.navigateTo({
				url: '/sub_user/pages/poster/index'
			})
		},
		//顾问端
		//钱包跳转
		adviserWalletLink(){
			uni.navigateTo({
				url:'/sub_adviser/pages/wallet/index'
			})
		},
		//我的团队
		adviserTeam(){
			uni.navigateTo({
				url:'/sub_adviser/pages/team/index'
			})
		}
	}
}
